button.circle-hover {
  position: relative;
  width: 18%;
  max-width:18%;
  height: 15.625rem;
  background-color: #FFF;
  color: #000;
  cursor: pointer;
  border-radius: 100%;
  border: 0.063rem solid #FFF;
  outline: none;
  margin: 3.125em;
}

button.circle-hover::before {
  content: ' ';
  background-color: rgba(250,250,250, 0.1);
  border-radius: 100%;
  position: absolute;
}

button.circle-hover:hover::before {
  top: -5.5rem;
  left: -5.5rem;
  bottom: -5.5rem;
  right: -5.5rem;
}

